export default {
  name: "ProjectModal",

  props: {
    value: {
      type: String,
      default: ""
    }
  },

  computed: {
    projectName: {
      get() {
        return this.value
      },
      set(val) {
        this.$emit('input', val)
      }
    }
  }
}
